import React, { Fragment } from "react";
import "./style.css";
import { Link } from "react-router-dom";

class Infolivrecontent extends React.Component {
  render() {
    let checkDessinateur;
    if (this.props.draw) {
      checkDessinateur = (
        <p>
          Dessinateurs.ices :{" "}
          <Link
            to={{
              pathname: `/infodessinateur/${this.props.draw.idDessinateur}`
            }}
          >
            {this.props.draw.prenom_dessinateur}{" "}
            {this.props.draw.nom_dessinateur}
          </Link>
        </p>
      );
    }

    let checkAuteur;
    if (this.props.author) {
      checkAuteur = (
        <p>
          Auteurs.rices :{" "}
          <Link
            to={{
              pathname: `/infoauteur/${this.props.author.idAuteur}`
            }}
          >
            {" "}
            {this.props.author.prenom_auteur} {this.props.author.nom_auteur}{" "}
          </Link>
        </p>
      );
    }

    console.log(this.props.books);
    return (
      <Fragment>
        <div className="espacelivre">
          <figure>
            <img src={this.props.books.visuel} alt="couverture" />
          </figure>
          <article className="descriptiflivre">
            <h2>{this.props.books.titre}</h2>

            {checkAuteur}
            {checkDessinateur}
            <p>Année de parution : {this.props.books.annee_pulication}</p>
            <p>
              Edition :{" "}
              <Link
                to={{
                  pathname: `/infoediteur/${this.props.edition.idEditeur}`
                }}
              >
                {" "}
                {this.props.edition.nom}
              </Link>{" "}
            </p>
            <p>Prix : {this.props.books.prix} €</p>
            <p>ISBN : {this.props.books.isbn}</p>
            <p>
              Chroniqué par
              <Link
                to={{
                  pathname: `/infoanimator/${this.props.animator.idIntervenant}`
                }}
              >
                {" "}
                {this.props.animator.prenom}
              </Link>
            </p>
            <p>
              Bdi
              <Link
                to={{
                  pathname: `/info/${this.props.books.soiree_id}`
                }}
              >
                {" "}
                #{this.props.books.soiree_id}
              </Link>
            </p>
          </article>
        </div>
      </Fragment>
    );
  }
}

export default Infolivrecontent;
