import React, { Fragment } from "react";
import "./style.css";
import { Link } from "react-router-dom";
import Moment from "moment";
import "moment/locale/fr";
import "./style.css";

class InfoContent extends React.Component {
  render() {
    let getAnimators;
    if (this.props.animators.length) {
      getAnimators = (
        <div className="animateurs">
          {this.props.animators.map(animators => (
            <div className="container_animateurs" key={animators.idIntervenant}>
              <Link
                to={{
                  pathname: `/infoanimator/${animators.idIntervenant}`
                }}
                key={animators.idIntervenant}
              >
                <p key={animators.idIntervenant}>{animators.prenom}</p>
              </Link>
            </div>
          ))}
        </div>
      );
    }

    let checkPhotos;
    if (this.props.photos.length === 0) {
      checkPhotos = (
        <div className="photos">
          <p>Pas de photos pour le moment</p>
        </div>
      );
    } else {
      checkPhotos = (
        <div className="photos">
          {this.props.photos.map(photos => (
            <div key={photos.idPhoto} className="container">
              <img
                key={photos.idPhoto}
                alt="photos de la soiree"
                src={this.props.photos.src}
              />
              <div key={photos.idPhoto} className="overlay" />
            </div>
          ))}
        </div>
      );
    }

    let checkCroquis;
    if (this.props.croquis.length === 0) {
      checkCroquis = (
        <div className="dessins">
          <p>Pas de croquis pour le moment</p>
        </div>
      );
    } else {
      checkCroquis = (
        <div className="dessins">
          {this.props.croquis.map(croquis => (
            <div className="container">
              <img alt="croquis de la soirée" src={croquis.src} />
              <div className="overlay">
                <p>{croquis.auteur}</p>
              </div>
            </div>
          ))}
        </div>
      );
    }
    Moment.locale("fr");

    return (
      <Fragment>
        <article className="current_description">
          <p>{this.props.description}</p>
          {/* idée : bouton "toutes les bdi"  */}
        </article>
        <section className="petite_section">
          <p>Avec</p> {getAnimators}
          {/* <hr className="designraw" /> */}
        </section>
        <section className="petite_section">
          <p>Les livres :</p>
          <div className="livres">
            {this.props.livres.map(books => (
              <div className="container_animateurs">
                <Link to={{ pathname: `/infolivre/${books.idLivre}` }}>
                  <p>{books.titre}</p>
                </Link>
              </div>
            ))}
          </div>
        </section>

        <section className="petite_section">
          <p>Les éditions :</p>
          <div className="livres">
            {this.props.livres.map(editions => (
              <div className="container_animateurs">
                <Link to={{ pathname: `/infoediteur/${editions.idEditeur}` }}>
                  <p>{editions.nom}</p>
                </Link>
              </div>
            ))}
          </div>
        </section>
        <section className="petite_section">
          <p id="p_sites">Retrouvez ces livres en librairie</p>
          <a
            href="https://www.parislibrairies.fr/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              className="logo_sites"
              alt="logo Paris Librairie"
              src="https://static.epagine.fr/mediaweb3/5435/logo_parislibrairie.jpg"
            />
          </a>
          <a
            href="https://www.placedeslibraires.fr/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              className="logo_sites"
              alt="logo place des libraires"
              src="https://static.epagine.fr/mediaweb3/7/logo_pdl.png"
              rel="noopener noreferrer"
            />
          </a>
        </section>

        <section className="section">
          <h2 className="format_titre">Croquis de la soirée</h2>
          {/* <hr className="designraw" /> */}
        </section>
        {checkCroquis}

        <section className="section">
          <h2 className="format_titre">Photos de la soirée</h2>
          {/* <hr className="designraw" /> */}
        </section>
        {checkPhotos}
      </Fragment>
    );
  }
}

export default InfoContent;
