import React, { Fragment } from "react";
import Header from "./../../Commun/Header";
import Navigation from "./../../Commun/Navigation";
import Footer from "./../../Commun/Footer";
import Editeurscontent from "./Content";
import axios from "axios";
import PathToBack from "./../../../PathToBack";

class Editeurs extends React.Component {
  state = {
    editions: []
  };

  componentDidMount() {
    axios.get(`${PathToBack}/editions`).then(res => {
      const editions = res.data.results;

      this.setState({ editions });
    });
  }
  render() {
    console.log(this.state.editions);
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Editeurscontent details={this.state.editions} />
        <Footer />
      </Fragment>
    );
  }
}

export default Editeurs;
