import React, { Fragment } from "react";
import Header from "./../Commun/Header";
import Navigation from "./../Commun/Navigation";
import Footer from "./../Commun/Footer";
import Infoanimatorcontent from "./Content";
import axios from "axios";
import PathToBack from "./../../PathToBack";

class infoanimator extends React.Component {
  constructor(props) {
    super(props);
    sessionStorage.setItem(
      "id_animator",
      this.props.match.params.idIntervenant
    );
    this.state = {
      oneAnimator: [],
      books: [],
      events: []
    };
  }
  componentDidMount() {
    let currentAnimator = sessionStorage.getItem("id_animator");
    axios
      .get(`${PathToBack}/get_animators_by_id/${currentAnimator}`)
      .then(res => {
        const oneAnimator = res.data.results[0];
        this.setState({ oneAnimator });
        console.log(this.state.oneAnimator);
      });

    axios
      .get(`${PathToBack}/get_books_by_animator/${currentAnimator}`)
      .then(res => {
        const books = res.data.results;
        this.setState({ books });
        console.log(this.state.books);
      });
    axios
      .get(`${PathToBack}/get_events_of_animators/${currentAnimator}`)
      .then(res => {
        const events = res.data.results;
        this.setState({ events });
        console.log(this.state.events);
      });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Infoanimatorcontent
          details={this.state.oneAnimator}
          books={this.state.books}
          events={this.state.events}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default infoanimator;
