import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import "./style.css";
class Navform extends React.Component {
  render() {
    return (
      <Fragment className="listing">
        <nav className="navform">
          <ul>
            <li>
              <Link to={{ pathname: `/` }}>Soirées</Link>
            </li>

            <li>
              <Link to={{ pathname: `/bdi` }}>Intervenants</Link>
            </li>

            <li>
              <Link to={{ pathname: `/bdi` }}>Livres</Link>
            </li>
            <li>
              <Link to={{ pathname: `/bdi` }}>Auteurs/Dessinateurs</Link>
            </li>
          </ul>
        </nav>
      </Fragment>
    );
  }
}

export default Navform;
