import React, { Component, Fragment } from "react";
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";
import Accueil from "./component/Views/Accueil";
import Info from "./component/Info";
import Livres from "./component/Views/Livres";
import Bdi from "./component/Views/Bdi";
import Infoanimator from "./component/Infoanimator";
import Intervenant from "./component/Views/Intervenant";
import Editeurs from "./component/Views/Editeurs";
import Auteurs from "./component/Views/Auteurs";
import Infoediteur from "./component/Infoediteur";
import Infoauteur from "./component/Infoauteur";
import Formulaire from "./component/Formulaire";
import Mentions from "./component/Views/Mentions";
import Infolivres from "./component/Infolivre";
import "./App.css";
import Presentation from "./component/Views/Presentation";
import Log from "./component/Log";

class App extends Component {
  render() {
    return (
      <Fragment>
        <Router>
          <Switch>
            <Route exact path="/" component={Accueil} />
            <Route path="/info/:idSoiree" component={Info} />
            <Route path="/bdi" component={Bdi} />
            <Route path="/intervenants" component={Intervenant} />
            <Route
              path="/infoanimator/:idIntervenant"
              component={Infoanimator}
            />

            <Route path="/livres" component={Livres} />
            <Route path="/editeurs" component={Editeurs} />
            <Route path="/auteurs" component={Auteurs} />
            <Route path="/infoediteur/:idEditeur" component={Infoediteur} />
            <Route path="/infoauteur/:idAuteur" component={Infoauteur} />
            <Route
              path="/infodessinateur/:idDessinateur"
              component={Infoauteur}
            />
            <Route path="/admin" component={Formulaire} />
            <Route path="/mentions" component={Mentions} />
            <Route path="/presentation" component={Presentation} />
            <Route path="/infolivre/:idLivre" component={Infolivres} />
            <Route path="/log" component={Log} />
          </Switch>
        </Router>
      </Fragment>
    );
  }
}

export default App;
