import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
class LivreContent extends React.Component {
  render() {
    console.log(this.props.details);
    return (
      <div className="espacebloc">
        {this.props.details.map(livre => (
          <div className="bloc_livre">
            <Link
              to={{
                pathname: `/infolivre/${livre.idLivre}`
              }}
            >
              <div className="descriptif_livre">
                <figure>
                  <img src={livre.visuel} alt="couverture" />
                </figure>
                <p>{livre.titre}</p>
              </div>
            </Link>
          </div>
        ))}
      </div>
    );
  }
}

export default LivreContent;
