import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
class Editeurscontent extends React.Component {
  render() {
    return (
      <div className="espacebloc">
        {this.props.details.map(content => (
          <div className="bloc_editeur">
            <Link
              to={{
                pathname: `infoediteur/${content.idEditeur}`
              }}
            >
              <div className="descriptif_editeur">
                <p className="nomediteur">edition {content.nom}</p>
              </div>
            </Link>
          </div>
        ))}
      </div>
    );
  }
}

export default Editeurscontent;
