import React, { Fragment } from "react";
import axios from "axios";
import "./style.css";
import Moment from "moment";
import "moment/locale/fr";
import PathToBack from "../../../PathToBack";
class Soireesform extends React.Component {
  state = {
    idSoiree: "",
    date_soiree: "",
    nom: "",
    description: "",
    image: "",
    lieu: "",
    heure: "",
    events: [],
    id: ""
  };

  componentDidMount() {
    axios.get(PathToBack + "/events").then(res => {
      const events = res.data.results;
      this.setState({ events });
      console.log(this.state.events);
    });
  }

  handleChange = event => {
    const name = event.target.name;
    const value = event.target.value;

    this.setState({
      [name]: value
    });
  };

  handleSubmit = event => {
    event.preventDefault();

    axios
      .post(PathToBack + "/events", {
        idSoiree: this.state.idSoiree,
        date_soiree: this.state.date_soiree,
        nom: this.state.nom,
        description: this.state.description,
        image: this.state.image,
        lieu: this.state.lieu,
        heure: this.state.heure
      })
      .then(res => {
        console.log(res);
        console.log(res.data);
      });
  };

  handleDelete = event => {
    this.setState({ id: event.target.value });
  };

  handleSub = event => {
    event.preventDefault();

    axios.delete(PathToBack + `/events/${this.state.id}`).then(res => {
      console.log(res);
      console.log(res.data);
      window.location.reload();
    });
  };

  render() {
    console.log(this.state.lieu);
    console.log(this.state.heure);
    console.log(this.state.id);
    return (
      <Fragment>
        <h2 className="hform">Soirées</h2>
        <section className="espace_form">
          <form className="crud" onSubmit={this.handleSubmit}>
            <label>
              idSoirée
              <input type="text" name="idSoiree" onChange={this.handleChange} />
            </label>
            <label>
              <br />
              Date
              <input
                type="date"
                name="date_soiree"
                onChange={this.handleChange}
              />
            </label>
            <label>
              Nom
              <input type="text" name="nom" onChange={this.handleChange} />
            </label>
            <label>
              Description
              <input
                type="text"
                name="description"
                onChange={this.handleChange}
              />
            </label>
            <label>
              Image
              <input type="text" name="image" onChange={this.handleChange} />
            </label>
            <label>
              Lieu
              <input type="text" name="lieu" onChange={this.handleChange} />
            </label>{" "}
            <label>
              Heure
              <input type="text" name="heure" onChange={this.handleChange} />
            </label>
            <button className="plus" type="submit">
              Valider
            </button>
          </form>
        </section>
        <h2 className="hform">Historique</h2>
        <section className="liste">
          <table>
            <thead>
              <tr>
                <th>ID</th>
                <th>Date</th>
                <th>Nom</th>
                <th>Description</th>
                <th>Image</th>
                <th>Lieux</th>
                <th>Heure</th>
                <th>Supprimer</th>
                <th>Modifier</th>
              </tr>
            </thead>
            <tbody>
              {this.state.events.map(events => (
                <tr>
                  <td>{events.idSoiree}</td>
                  <td>{Moment(events.date_soiree).format(" dddd LL")}</td>
                  <td>{events.nom}</td>
                  <td>
                    <div className="scroll">{events.description}</div>
                  </td>
                  <td>
                    {" "}
                    <div className="scroll">{events.image}</div>
                  </td>
                  <td>{events.lieu}</td>
                  <td>{events.heure}</td>
                  <td>
                    <button type="submit">Supprimer</button>{" "}
                  </td>
                  <td>
                    <button type="submit">Modifier</button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <br />
          {/* <div>
            <form onSubmit={this.handleSub}>
              <label>
                Person ID:
                <input type="text" name="id" onChange={this.handleDelete} />
              </label>
              <button type="submit">Supprimer</button>
            </form>
          </div> */}
        </section>
      </Fragment>
    );
  }
}

export default Soireesform;
