import React from "react";
import "./style.css";
import axios from "axios";
import { Link } from "react-router-dom";
import PathToBack from "./../../../../PathToBack";

class Bloc extends React.Component {
  state = {
    events: []
  };

  componentDidMount() {
    axios.get(`${PathToBack}/events`).then(res => {
      const events = res.data.results;
      this.setState({ events });
      console.log(res.data.results);
    });
  }

  render() {
    return (
      <div className="espacebloc">
        {this.state.events.map(event => (
          <div className="bloc">
            <Link
              to={{
                pathname: `/info/${event.idSoiree}`
              }}
            >
              <div className="descriptif">
                <p>#{event.nom}</p>
              </div>
            </Link>
          </div>
        ))}
      </div>
    );
  }
}

export default Bloc;
