import React, { Fragment } from "react";
import Header from "./../../Commun/Header";
import Navigation from "./../../Commun/Navigation";
import Footer from "./../../Commun/Footer";
import Contentauteur from "./Content";
import axios from "axios";
import PathToBack from "./../../../PathToBack";
class Auteurs extends React.Component {
  state = {
    auteurs: [],
    dessinateurs: []
  };

  componentDidMount() {
    axios.get(`${PathToBack}/auteurs`).then(res => {
      const auteurs = res.data.results;
      this.setState({ auteurs });
      console.log(this.state.auteurs);
    });
    axios.get(`${PathToBack}/dessinateurs`).then(res => {
      const dessinateurs = res.data.results;
      this.setState({ dessinateurs });
      console.log(this.state.dessinateurs);
    });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Contentauteur
          auteurs={this.state.auteurs}
          dessinateurs={this.state.dessinateurs}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default Auteurs;
