import React, { Fragment } from "react";
import Header from "./../Commun/Header";
import Navigation from "./../Commun/Navigation";
import Footer from "../Commun/Footer";
import axios from "axios";
import Vignette from "../Commun/Vignette";
import Infocontent from "./Content";
import PathToBack from "./../../PathToBack";
class Info extends React.Component {
  constructor(props) {
    super(props);
    sessionStorage.setItem("id_soiree", this.props.match.params.idSoiree);

    console.log(this.props.match.params.idSoiree);
    this.state = {
      oneEvent: [],
      animatorsOfEvent: [],
      croquis: [],
      photos: [],
      livres: []
    };
  }

  componentDidMount() {
    let currentSoiree = sessionStorage.getItem("id_soiree");
    axios.get(`${PathToBack}/croquis/${currentSoiree}`).then(res => {
      const croquis = res.data.results;
      this.setState({ croquis });
    });
    axios.get(`${PathToBack}/get_one_event/${currentSoiree}`).then(event => {
      const oneEvent = event.data.results[0];
      this.setState({ oneEvent });
      console.log(event.data.results[0]);
    });
    axios
      .get(
        `${PathToBack}/get_animators_of_event/${currentSoiree}
        `
      )
      .then(animators => {
        const animatorsOfEvent = animators.data.results;
        this.setState({ animatorsOfEvent });
        console.log(animators.data.results);
        console.log(animatorsOfEvent);
      });
    axios
      .get(
        `  ${PathToBack}/get_books_by_event/${currentSoiree}
        `
      )
      .then(book => {
        const livres = book.data.results;
        this.setState({ livres });

        console.log(livres);
      });
  }

  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Vignette details={this.state.oneEvent} />
        <Infocontent
          details={this.state.oneEvent}
          croquis={this.state.croquis}
          photos={this.state.photos}
          description={this.state.oneEvent.description}
          animators={this.state.animatorsOfEvent}
          livres={this.state.livres}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default Info;
