import React, { Fragment } from "react";
import "./style.css";
import { Link } from "react-router-dom";

class Infoauteurcontent extends React.Component {
  render() {
    let checkDetails;
    if (this.props.details.prenom) {
      checkDetails = <p>{this.props.details.prenom}</p>;
    } else if (this.props.details.nom) {
      checkDetails = <p> {"edition " + this.props.details.nom} </p>;
    } else if (this.props.details.nom_auteur) {
      checkDetails = (
        <p>
          {this.props.details.nom_auteur} {this.props.details.prenom_auteur}{" "}
        </p>
      );
    } else if (this.props.details.nom_dessinateur) {
      checkDetails = (
        <p>
          {this.props.details.nom_dessinateur}{" "}
          {this.props.details.prenom_dessinateur}
        </p>
      );
    }

    let checkActivite;
    if (this.props.details.site) {
      checkActivite = (
        <div className="carte_activite">
          <p>
            <a
              href={this.props.details.site}
              target="_blank"
              rel="noopener noreferrer"
            >
              Site officiel
            </a>
          </p>
        </div>
      );
    } else {
      checkActivite = (
        <div className="carte_activite">
          <p>{this.props.details.activite}</p>
        </div>
      );
    }
    console.log(this.props.books);
    console.log(this.props.dessinateursBooks);
    console.log(this.props.editeursBooks);

    let checkBooks;
    if (this.props.books.length !== 0) {
      checkBooks = (
        <section className="books_contents">
          {this.props.books.map(livre => (
            <div className="bloc_livre">
              <Link
                to={{
                  pathname: `/infolivre/${livre.idLivre}`
                }}
              >
                <div className="descriptif_livre_content">
                  <figure>
                    <img src={livre.visuel} alt="couverture" />
                    <p>{livre.titre}</p>
                  </figure>
                </div>
              </Link>
            </div>
          ))}
        </section>
      );
    } else if (this.props.dessinateursBooks) {
      checkBooks = (
        <section className="books_contents">
          {this.props.dessinateursBooks.map(livre => (
            <div className="bloc_livre">
              <Link
                to={{
                  pathname: `#`
                }}
              >
                <div className="descriptif_livre_content">
                  <figure>
                    <img src={livre.visuel} alt="couverture" />
                    <p>{livre.titre}</p>
                  </figure>
                </div>
              </Link>
            </div>
          ))}
        </section>
      );
    }
    return (
      <Fragment>
        <section className="espacebloc_content">
          <div className="carte_prenom">{checkDetails}</div>
          {checkActivite}
        </section>
        <h2 className="titre_content">Livres</h2>
        {checkBooks}
      </Fragment>
    );
  }
}

export default Infoauteurcontent;
