import React, { Fragment } from "react";
import Moment from "moment";
import { connect } from "react-redux";
import "moment/locale/fr";
import "./style.css";

const mapStateToprops = state => {
  return {
    lastEvent: state.lastEvent
  };
};

class VignetteClass extends React.Component {
  render() {
    Moment.locale("fr");

    return (
      <Fragment>
        <div className="vignette">
          <img
            alt="Vignette des bd de la soirée"
            src={this.props.details.image}
          />
          <div className="bandeau">
            <h2>
              bande des idées
              <span className="bbnom">#{this.props.details.nom}</span>
            </h2>
            <div className="date">
              <p>{Moment(this.props.details.date_soiree).format(" dddd LL")}</p>
              <p className="purple">
                {this.props.details.heure} - {this.props.details.lieu}
              </p>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default connect(mapStateToprops)(VignetteClass);
