import React, { Component, Fragment } from "react";
import Vignette from "./../../../Commun/Vignette";
import axios from "axios";
import { Link } from "react-router-dom";
import { lastEvent } from "../../../../redux/actions";
import { connect } from "react-redux";
import PathToBack from "../../../../PathToBack";
const mapDispatchToProps = dispatch => {
  return { lastEvent: event => dispatch(lastEvent(event)) };
};
class AccueilClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastEvent: []
    };
  }

  componentDidMount() {
    axios
      .get(PathToBack + "/get_lastevent")
      .then(res => {
        const lastEvent = res.data.results[0];
        this.setState({ lastEvent });

        console.log(this.state.lastEvent);
        console.log(res.data.results[0]);
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  render() {
    return (
      <Fragment>
        <Vignette details={this.state.lastEvent} />
        <Link
          to={{
            pathname: `/info/${this.state.lastEvent.idSoiree}`
          }}
        >
          <button className="plus">En savoir plus</button>
        </Link>
      </Fragment>
    );
  }
}

const AccueilContent = connect(
  null,
  mapDispatchToProps
)(AccueilClass);
export default AccueilContent;
