import React, { Fragment } from "react";
import axios from "axios";
import Header from "./../Commun/Header";
import Navigation from "./../Commun/Navigation";
import Footer from "./../Commun/Footer";
import Infoanimatorcontent from "./../Infoanimator/Content";
import PathToBack from "./../../PathToBack";

class Infoediteur extends React.Component {
  constructor(props) {
    super(props);
    sessionStorage.setItem("id_edition", this.props.match.params.idEditeur);
    console.log(this.props.match.params.idEditeur);
    this.state = {
      oneEdition: [],
      editeursBooks: []
    };
  }
  componentDidMount() {
    let currentEdition = sessionStorage.getItem("id_edition");
    axios
      .get(`${PathToBack}/get_chosen_edition/${currentEdition}`)
      .then(res => {
        const oneEdition = res.data.results[0];
        this.setState({ oneEdition });
        console.log(this.state.oneEdition);
      });
    axios
      .get(`${PathToBack}/get_books_by_edition/${currentEdition}`)
      .then(res => {
        const editeursBooks = res.data.results;
        this.setState({ editeursBooks });
        console.log(this.state.editeursBooks);
      });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Infoanimatorcontent
          details={this.state.oneEdition}
          books={this.state.editeursBooks}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default Infoediteur;
