import React, { Fragment } from "react";
import Header from "./../../Commun/Header";
import Navigation from "./../../Commun/Navigation";
import Footer from "./../../Commun/Footer";
import LivreContent from "./Content";
import axios from "axios";
import PathToBack from "./../../../PathToBack";
class Livres extends React.Component {
  state = {
    livres: []
  };

  componentDidMount() {
    axios.get(`${PathToBack}/books`).then(res => {
      const livres = res.data.results;
      this.setState({ livres });
      console.log(this.state.livres);
    });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <LivreContent details={this.state.livres} />
        <Footer />
      </Fragment>
    );
  }
}

export default Livres;
