import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
class Contentanimator extends React.Component {
  render() {
    return (
      <div className="espacebloc">
        {this.props.details.map(content => (
          <div className="bloc_content">
            <Link
              to={{
                pathname: `infoanimator/${content.idIntervenant}`
              }}
            >
              <div className="descriptif_content">
                <p className="prenom">{content.prenom}</p>
                <p className="activite">{content.activite}</p>
              </div>
            </Link>
          </div>
        ))}
      </div>
    );
  }
}

export default Contentanimator;
