import React, { Fragment } from "react";
import axios from "axios";
import Header from "./../Commun/Header";
import Navigation from "./../Commun/Navigation";
import Footer from "./../Commun/Footer";
import Infoauteurcontent from "./Content";
import PathToBack from "./../../PathToBack";

class Infoauteur extends React.Component {
  constructor(props) {
    super(props);
    sessionStorage.setItem("id_auteur", this.props.match.params.idAuteur);
    sessionStorage.setItem(
      "id_dessinateur",
      this.props.match.params.idDessinateur
    );
    console.log(this.props.match.params.idAuteur);
    this.state = {
      oneAuteur: [],
      oneDessinateur: [],
      dessinateursBooks: [],
      auteursBooks: []
    };
  }
  componentDidMount() {
    let currentAuteur = sessionStorage.getItem("id_auteur");
    let currentDessinateur = sessionStorage.getItem("id_dessinateur");

    axios.get(`${PathToBack}/get_chosen_auteur/${currentAuteur}`).then(res => {
      const oneAuteur = res.data.results[0];
      this.setState({ oneAuteur });
      console.log(this.state.oneAuteur);
      axios
        .get(`${PathToBack}/get_books_by_auteur/${currentAuteur}`)
        .then(res => {
          const auteursBooks = res.data.results;
          console.log("aleeeeeeeeeeeeed", res.data.results);
          this.setState({ auteursBooks });
          console.log(this.state.auteursBooks);
        });
    });

    axios
      .get(`${PathToBack}/get_chosen_dessinateur/${currentDessinateur}`)
      .then(res => {
        const oneDessinateur = res.data.results[0];
        this.setState({ oneDessinateur });
        console.log(this.state.oneDessinateur);
        axios
          .get(`${PathToBack}/get_books_by_dessinateur/${currentDessinateur}`)
          .then(res => {
            const dessinateursBooks = res.data.results;
            console.log(res.data.results);
            this.setState({ dessinateursBooks });
          });
      });
  }
  render() {
    console.log(this.state.dessinateursBooks);
    console.log(this.state.oneDessinateur);
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Infoauteurcontent
          details={this.state.oneAuteur || this.state.oneDessinateur}
          books={this.state.auteursBooks}
          dessinateursBooks={this.state.dessinateursBooks}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default Infoauteur;
