import React from "react";
import { Link } from "react-router-dom";
import "./style.css";

class Header extends React.Component {
  render() {
    return (
      <header>
        <Link to={{ pathname: `/` }}>
          <h1>la bande des idées</h1>
        </Link>
        <p>Le rendez-vous mensuel des bédéphiles</p>
      </header>
    );
  }
}

export default Header;
