import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
class Auteurscontent extends React.Component {
  render() {
    return (
      <div className="espacebloc">
        <h2 className="auteur_titre">Auteurs.rices</h2>
        <section className="auteurs">
          {this.props.auteurs.map(content => (
            <div className="bloc_auteur">
              <Link
                to={{
                  pathname: `infoauteur/${content.idAuteur}`
                }}
              >
                <div className="descriptif_auteur">
                  <p className="nomauteur">
                    {content.prenom_auteur} {content.nom_auteur}
                  </p>
                </div>
              </Link>
            </div>
          ))}
        </section>
        <h2 className="auteur_titre">Dessinateurs.rices</h2>
        <section className="dessinateurs">
          {this.props.dessinateurs.map(content => (
            <div className="bloc_auteur">
              <Link
                to={{
                  pathname: `infodessinateur/${content.idDessinateur}`
                }}
              >
                <div className="descriptif_auteur">
                  <p className="nomauteur">
                    {content.prenom_dessinateur} {content.nom_dessinateur}
                  </p>
                </div>
              </Link>
            </div>
          ))}
        </section>
      </div>
    );
  }
}

export default Auteurscontent;
