import React, { Fragment } from "react";
import Header from "./../Commun/Header";
import Navigation from "./../Commun/Navigation";
import Footer from "./../Commun/Footer";
import Infolivrecontent from "./../Infolivre/Content";
import PathToBack from "./../../PathToBack";
import axios from "axios";

class Infolivre extends React.Component {
  constructor(props) {
    super(props);

    sessionStorage.setItem("id_livre", this.props.match.params.idLivre);

    this.state = {
      oneBook: [],
      animator: [],
      edition: [],
      author: [],
      draw: []
    };
  }
  componentDidMount() {
    let currentBook = sessionStorage.getItem("id_livre");
    console.log(currentBook);

    axios.get(`${PathToBack}/get_chosen_book/${currentBook}`).then(res => {
      const oneBook = res.data.results[0];
      this.setState({ oneBook });
      console.log(this.state.oneBook);
    });

    axios.get(`${PathToBack}/get_animator_of_book/${currentBook}`).then(res => {
      const animator = res.data.results[0];
      this.setState({ animator });
      console.log(this.state.animator);
    });

    axios.get(`${PathToBack}/get_edition_by_book/${currentBook}`).then(res => {
      const edition = res.data.results[0];
      this.setState({ edition });
      console.log(this.state.edition);
    });

    axios
      .get(`${PathToBack}/get_dessinateurs_by_book/${currentBook}`)
      .then(res => {
        const draw = res.data.results[0];
        this.setState({ draw });
        console.log(this.state.draw);
      });

    axios.get(`${PathToBack}/get_auteurs_by_book/${currentBook}`).then(res => {
      const author = res.data.results[0];
      this.setState({ author });
      console.log(this.state.author);
      console.log(res.data.results);
    });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Infolivrecontent
          books={this.state.oneBook}
          animator={this.state.animator}
          edition={this.state.edition}
          author={this.state.author}
          draw={this.state.draw}
        />
        <Footer />
      </Fragment>
    );
  }
}

export default Infolivre;
