import React, { Fragment } from "react";
import Header from "./../../Commun/Header";
import Navigation from "./../../Commun/Navigation";
import Footer from "./../../Commun/Footer";
import PathToBack from "./../../../PathToBack";
import axios from "axios";
import Contentanimator from "./Content";
class Intervenant extends React.Component {
  state = {
    animators: []
  };

  componentDidMount() {
    axios.get(`${PathToBack}/animators`).then(res => {
      const animators = res.data.results;
      this.setState({ animators });
    });
  }
  render() {
    return (
      <Fragment>
        <Header />
        <Navigation />
        <Contentanimator details={this.state.animators} />
        <Footer />
      </Fragment>
    );
  }
}

export default Intervenant;
